/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Libre Franklin", sans-serif;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --Blue: hsl(223, 87%, 63%);
  --Pale-Blue: hsl(223, 100%, 88%);
  --Light-Red: hsl(354, 100%, 66%);
  --Gray: hsl(0, 0%, 59%);
  --Dark-Blue: hsl(209, 33%, 12%);
}

/*Pixels to rems*/
.header {
  text-align: center;
}
.header__image {
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  margin-top: 5.3125rem;
  margin-bottom: 2.8125rem;
}
.header__image img {
  height: 1.0625rem;
  width: 3.5rem;
}
.header__headline {
  font-size: 1.125rem;
  color: var(--Gray);
  letter-spacing: 1px;
  font-weight: 300;
}
.header__headline-bold {
  font-weight: 700;
  color: var(--Dark-Blue);
}
.header__text {
  color: var(--Dark-Blue);
  font-size: 0.6875rem;
  margin-top: 1.5625rem;
}

.form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 4.375rem;
}
.form__invalid {
  display: none;
}
.form__btn input {
  width: 17.6875rem;
  height: 2.625rem;
  background: var(--Blue);
  border-radius: 1.5625rem;
  color: #fff;
  border: none;
  cursor: pointer;
}
.form__email {
  margin-bottom: 0.625rem;
  position: relative;
}
.form__email input {
  border: 1px solid var(--Pale-Blue);
  width: 17.6875rem;
  height: 2.625rem;
  border-radius: 1.5625rem;
  font-size: 0.6875rem;
  padding-left: 1.25rem;
}
.form__email input:focus-visible:invalid ~ .form__invalid {
  display: inline-block;
  position: absolute;
  left: 50%;
  bottom: -25px;
  translate: -50%;
  color: var(--Light-Red);
  font-size: 0.6875rem;
  width: 100%;
}
.form__email input::placeholder {
  font-size: 0.6875rem;
  color: var(--Pale-Blue);
  padding-left: 33px;
}

.margin {
  margin-top: 1.5625rem;
}

.slack {
  padding: 0 28px;
  text-align: center;
  margin-bottom: 7.5rem;
}
.socials {
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
}
.socials__icons {
  display: flex;
  justify-content: space-between;
  width: 9.375rem;
}
.socials__icons i {
  font-size: 2.0625rem;
  color: var(--Blue);
}

.copyright {
  text-align: center;
  font-size: 0.75rem;
  color: var(--Gray);
  margin-top: 2.5rem;
}